import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
import uniNumberBox from '@/components/uni-number-box.vue'
import uniBadge from "@/components/uni-badge/uni-badge.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		uniNumberBox,
		uniBadge,
		uniPopup,
		uniCountdown
	},
	data() {
		return {
			id: '',
			groupId:'',	//参团ID, 如果该值不为空表明是参团而不是开团
			unit: 1,
			groupbuy:{},
			groupBuyCountDown:'',	//倒计时
			actionFlag:false,	//是否开始团购标记
			endFlag:false,	//是否已经结束
			interval:null,	//定时器检测团购是否开始或者结束
			product: {
				unitPrice:0,
				unitPriceStandard:0,
				soldUnit: 0,
				totalUnit: 0,
				productMainImage:{},
				productImages:[],
				attrList:[]
			},
			productSku: {},
			cartNum: 0,
			// productSkuAttrValueMap:null,
			isProductCollected: false,
			specClass: 'none',
			specSelected: [],
			favorite: true,
			type: '',
			commentList: [],
			totalComment: 0,
			stars: [0, 0, 0, 0, 0]
		};
	},
	onLoad(options) {

		let id = options.id;
		if (id) {
			this.id = id;
			this.inquiryGroupBuy(id);
		}
		this.groupId = options.groupId;
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
		shareHref() {
			let pages = getCurrentPages()
			let page = pages[pages.length - 1]

			return this.$api.request.apiBaseUrl.replace("/b2c/rest/","") + '/#/' + page.route + '?id=' + this.product.productUuid;






		}
	},
	methods: {
		...mapMutations(['login', 'addFootPrint']),

		togglePopup(type, open) {
			switch (type) {
				case 'top':
					this.content = '顶部弹出 popup'
					break

				case 'bottom':
					this.content = '底部弹出 popup'
					break
				case 'center':
					this.content = '居中弹出 popup'
					break
			}
			this.type = type
			this.$nextTick(() => {
				this.$refs['show' + open].open()
			})
		},
		closeShare() {
			this.$refs.showshare.close();
		},
		closeAttr() {
			this.$refs.showattr.close();
		},
		cancel(type) {
			this.$refs.showshare.close();
		},
		change(e) {
			console.log('是否打开:' + e.show)
		},
		//查询团购商品
		inquiryGroupBuy(id) {
			let postData = {
				groupBuyProductUuid: id
			}
			this.$api.request.groupBuy(postData, res => {
				if (res.body.status.statusCode === '0') {
					this.groupbuy = res.body.data;
					this.product = this.groupbuy.productDTO;
					if (this.product.skuEnabled) {
						this.productSku = this.product.skuList[0];
					}
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		},
		numberChange(e) {
			this.unit = e.number;
		},
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(productSku) {
			this.productSku = productSku;

		},
		//立即购买
		buy() {
			var url = '/pages/order/createGroupBuyOrder?groupBuyId='+this.groupId+'&groupBuyProductId='+this.id+'&productId='+this.product.productUuid+'&productSkuId='+this.productSku.productSkuUuid+'&unit='+this.unit;
			debugger
			if(this.userInfo && this.userInfo.userUuid){
				uni.navigateTo({
					url: url
				})
			}else{
				uni.navigateTo({
					url: '/pages/public/login?to='+escape(url) 
				})
			}
		},
		navTo(url) {
			uni.navigateTo({
				url: url
			})
		},
		previewImage(url) {
			var urls = [];
			urls.push(url);
			uni.previewImage({
				current: url,
				indicator: "number",
				loop: "true",
				urls: urls
			})
		},
		stopPrevent() {}
	},
	onUnload(){
		
	}
}
